	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>the federal government has access to data pertaining to labour market trends;</p>\n</li>\n<li>\n<p>the government has undertaken an Atlantic Growth Strategy dedicated to building a vibrant economic future for Atlantic Canada;</p>\n</li>\n<li>\n<p>thesuccessful implementation of this strategy will result in youth retention, job growth, and attraction to Atlantic Canada.</p>\n</li>\n</ul>\n<p><strong>BE IT RESOLVED</strong> the Liberal Party of Canada urges the Government of Canada to adopt a pilot to support Atlantic post-secondary education institutions in partnering with industry and employers to match programming and students with labour market trends and job opportunities in the region.(Nova Scotia)</p>","frontmatter":{"path":"/policy/post-secondary-education-and-labour-market-partnership","date":"Tue Mar 06 2018 00:00:26 GMT-0500 (EST)","title":"Post-Secondary Education and Labour Market Partnership"}}},"pathContext":{}}

